@media (min-width: 600px){

body {
    padding: 0;
    margin: 0;
    font-family: 'Roboto', sans-serif;
    /* background-color: ; */
}

.container {
    width: 950px;
    margin: 0 auto;
}
.footerBackgroundColor {
    background-color: rgba(36, 36, 33, .9);
}

.main_loader {
    text-align: center;
    margin-top: 100px;
}

footer .logo {
    text-align: center;
    font-family: 'oswald', sans-serif;
    ;
    color: #f8333c;
    font-size: 40px;
    padding: 11px 0px;
    border-bottom: 1px solid #4d4d4d;
}

footer .wrapper {
    display: flex;
    color: #f8333c;
}

footer .left,
footer .right {
    flex: 1;
}

footer .business_nfo {
    display: flex;
    flex-wrap: wrap;
}

footer .business_nfo .tag {
    flex-grow: 1;
    position: relative;
    width: 50%;
    margin-bottom: 30px;
}

footer .business_nfo .icon {
    position: absolute;
    font-size: 26px;
    top: 10px;
}

footer .business_nfo .nfo {
    padding-left: 40px;
}

.user_container {
    display: flex;
    margin-bottom: 50px;
    min-height: 700px;
}

.user_container .user_left_nav {
    border-right: 1px solid #f8f8f8;
}

.user_container .user_right {
    flex-grow: 1;
    padding: 0 0 0 29px;
}


}

.page_container {
    min-height: 75vh;
    padding-top: 125px;
}

.page_wrapper {
    padding-top: 50px;
}


.font_oswald {
    font-family: 'oswald', sans-serif;
}


a {
    text-decoration: none;
    color: inherit;
}

.main_loader {
    text-align: center;
    margin-top: 100px;
}


/* ================== */
     /* header */
/* ================== */


header {
    position: fixed;
    z-index: 999;
    width: 100%;
    background-color: rgba(36, 36, 33, .9);
    border-bottom: 1px solid rgba(36, 36, 33, .5);
}

header .header_logo_title {
    text-transform: uppercase;
    color: #f8333c;
    padding-bottom: 10px;
}

header .logo {
    font-family: 'oswald', sans-serif;
    color: #f8333c;
    font-size: 40px;
    padding: 11px 0px;
    text-align: left;
}

header .header_logo_venue {
    font-size: 40px;
    color: #f8333c;
}

header a {
    margin-left: 20px;
}

header .container {
    display: flex;
    
}

header .container .left {
    width: 250px;
}

header .container .right {
    flex-grow: 1;
    border-left: 1px solid #f8333c;
    border-right: 1px solid #f8333c;
}

header .container .right .top,
header .container .right .bottom {
    text-transform: uppercase;
    font-weight: 700;
    text-align: right;
    color: #f8333c;
    font-size: 15px;
    padding: 10px 20px;
}

header .container .right .top {
    color: #f8333c;
    border-bottom: 1px solid #f8333c;
}


header .log_out_link {
    cursor: pointer;
    display: inline-block;
    margin-left: 20px;
}

/* ================== */
     /* footer */
/* ================== */

.footerBackgroundColor {
    background-color: rgba(36, 36, 33, .9);
}

footer .logo {
    text-align: center;
    font-family: 'oswald', sans-serif;;
    color: #f8333c;
    font-size: 40px;
    padding: 11px 0px;
    border-bottom: 1px solid #4d4d4d;
}

footer .wrapper {
    display: flex;
    color: #f8333c;
}

footer .left,
footer .right {
    flex: 1;
}

footer .business_nfo {
    display: flex;
    flex-wrap: wrap;
}

footer .business_nfo .tag {
    flex-grow: 1;
    position: relative;
    width: 50%;
    margin-bottom: 30px;
}

footer .business_nfo .icon {
    position: absolute;
    font-size: 26px;
    top: 10px;
}

footer .business_nfo .nfo {
    padding-left: 40px;
}

/* ================== */
  /* Register/login */
/* ================== */
.register_login_container {
    display: flex;
    border: 1px solid #999592;
    border-top: 5px solid #999592;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin: 80px 0px 110px 0px;
    padding: 30px;
    min-height: 430px;
}

.register_login_container .left,
.register_login_container .right {
    flex: 1
}

.register_login_container h2 {
    margin: 20px 0px;
    text-transform: uppercase;
    color: #686868;
}

.register_login_container p {
    font-weight: 300;
    font-size: 16px;
    width: 90%;
}

/* ================== */
     /* About */
/* ================== */

.brogAbout {
    text-align: center;
    font-size: 3em;
    font-family: 'Inconsolata', monospace;
}

.brogAboutMe{
    font-size: 2em;
    text-align: center;
    font-family: 'Inconsolata', monospace;
}

.aboutFont {
    font-family: 'Inconsolata', monospace;
}
/* ================== */
     /* buttons */
/* ================== */

.link_default, button {
    text-transform: uppercase;
    padding: 10px;
    display: table;
    background: #e1ddc3;
    margin-top: 10px;
}

button, .link_default {
    cursor: pointer;
    text-transform: uppercase;
    padding: 10px;
    background: #e1ddc3;
    margin-top: 10px;
    font-family: 'Oswald', sans-serif;
    font-size: 16px;
}

/* ================== */
      /* Home */
/* ================== */

.homeContainer {
    display: flex;
    border: 1px solid #999592;
    border-top: 5px solid #999592;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0px 0px 110px 0px;
    padding: 30px;
    min-height: 430px;
}

.buttonPost {
    position: relative;
    
}

.welcome{
    font-family: 'Inconsolata', monospace;
    text-align: center;
    font-size: 4em;
    padding-top: 15px;
}

.welcomeSub{
    text-align: center;
    font-family: 'Inconsolata', monospace;
}


/* ================== */
      /* Form */
/* ================== */

input {
    font-size: 15px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 10px 10px;
    width: 100%;
    margin: 10px 0px 0px 0px;
}

.error_label {
    color: #F44336;
    display: block;
    font-size: 15px;
}

textarea {
    width: 100%;
    height: 51px;
    font-size: 15px;
    padding: 10px;
    box-sizing: border-box;
    margin-top: 5px;
    border: 1px solid #d1d1d1;
}

.form_devider {
    border-bottom: 3px solid #999592;
    margin: 40px 0px;
}

.label_inputs {
    display: inline-block;
    background: #999592;
    color: #fff;
    padding: 5px 5px;
}

select {
    font-size: 16px;
    height: 38px;
    border: 1px solid #e9e9e9;
    border-radius: 3px;
    width: 100%;
    margin: 10px 0px 0px 0px;
}

.formBlock {
    margin-bottom: 10px;
}

.form_block_two {
    display: flex;
}

.form_block_two .block {
    flex-grow: 1;
    box-sizing: border-box;
}

.form_block_two .block:nth-child(1) {
    padding-right: 10px;
}

.form_block_two .block:nth-child(2) {
    padding-left: 10px;
}

.form_success {
    background: #4CAF50;
    color: #ffffffff;
    padding: 10px;
    display: block;
}


/* ================== */
      /* POSTS */
/* ================== */
.postTitle{
    font-family: 'Inconsolata', monospace;
}

.postBody, .datePosted {
    font-family: 'Inconsolata', monospace;
}



/* ================== */
      /* USER */
/* ================== */

.user_container {
    display: flex;
    margin-bottom: 50px;
    min-height: 700px;
}

.user_container  .user_right {
    flex-grow: 1;
    padding: 0 0 0 29px;
}

h2 {
    padding-top: 30px;
}

.user_container .user_left_nav {
    border-right: 1px solid #f8f8f8;
}

.user_container .user_left_nav .links a {
    display: block;
    border-bottom: 1px solid #dddddd;
    font-size: 20px;
    font-weight: 300;
    padding: 10px 0px;
    width: 280px;
}

.user_container .user_nfo_panel {
    background: #f8f8f8;
    padding: 20px;
    margin-top: 20px;
}

.user_container .user_nfo_panel h1 {
    margin: 0 0 20px 0;
    line-height: 32px;
    text-transform: uppercase;
    font-size: 25px;
}


.user_container .user_nfo_panel span {
    display: block;
    font-size: 18px;
    font-weight: 300;
}

/* ================== */
    /* Carousel */
/* ================== */


.carousel {
  /* any dimensions are fine, it can be responsive with max-width */
  max-width: 0 auto;
  height: 800px;
  margin-top: 0;
  padding: 100px;
}

.carousel-inner {
  /* make sure your .items will get correct height */
  height: 100%;
}

.item {
  background-size: cover;
  background-position: 50% 50%;
  width: 100%;
  height: 100%;
}

.item img {
  visibility: hidden;
}